<template>
  <!--  -->
  <div class="dyo" :class="{dark: dark}">
    <!--  -->
    <canvas id="canvas"></canvas>
    
    <!--  -->
    <div id="c">
      <input id="file" type="file" @change="uploadFile" accept="image/*, .heic, .heif, .HEIC, .HEIF">
    </div>
  </div>
</template>

<script>

//  db  pica  heic2any
import { db } from '../db.js';
import Pica from 'pica';
import heic2any from 'heic2any';

export default {
  name: 'HelloWorld',
  props: {
    msg: String,
    dark: Boolean,
  },
  firestore: {
    photos: db.collection('photo'),
  },
  metaInfo: {
    title: '',
  },
  data() {
    return {
      photos: [],
      message: 'Hello Vue!',
      vueCanvas: null,
      painting: false,
      canvas: null,
      ctx: null,
      color: '#4060BF',
    };
  },
  methods: {
    async uploadFile(e) {

      const file = e.target.files[0]; // 
      
      if (!file) { // 
        return;
      }

      // 
      const isHeic = (file.type === '' || file.type === 'image/heic' || 
                     file.type === 'image/heif') && 
                     (file.name.toLowerCase().endsWith('.heic') || 
                     file.name.toLowerCase().endsWith('.heif'));

      try {
        let processedFile = file;
        //  HEIC/HEIF 
        if (isHeic) {
          console.log(' HEIC :', file.name);
          try {
            const blob = await heic2any({
              blob: file,
              toType: 'image/jpeg',
              quality: 0.95
            });
            
            const resultBlob = Array.isArray(blob) ? blob[0] : blob;
            processedFile = new File([resultBlob], file.name.replace(/\.(heic|HEIC|heif|HEIF)$/, '.jpg'), {
              type: 'image/jpeg'
            });
          } catch (heicError) {
            //  JPEG 
            if (heicError.message.includes('already browser readable')) {
              console.log('');
              processedFile = file;
            } else {
              console.error('HEIC :', heicError);
              window.alert('HEIC ');
              return;
            }
          }
        }

        const reader = new FileReader();
        reader.onload = (event) => {
          const img = new Image();
          img.onload = () => {
            // 
            const MAX_DIMENSION = 400;
            let targetWidth = img.width;
            let targetHeight = img.height;
            
            // 
            if (img.width > MAX_DIMENSION || img.height > MAX_DIMENSION) {
              if (img.width > img.height) {
                targetWidth = MAX_DIMENSION;
                targetHeight = Math.round(img.height * (MAX_DIMENSION / img.width));
              } else {
                targetHeight = MAX_DIMENSION;
                targetWidth = Math.round(img.width * (MAX_DIMENSION / img.height));
              }
            }
            
            //  canvas 
            this.canvas.width = targetWidth;
            this.canvas.height = targetHeight;
            
            //  pica 
            const pica = new Pica({
              features: ['js', 'wasm', 'cib'],
            });
            
            //  canvas 
            const tempCanvas = document.createElement('canvas');
            tempCanvas.width = targetWidth;
            tempCanvas.height = targetHeight;
            const tempCtx = tempCanvas.getContext('2d');
            tempCtx.drawImage(img, 0, 0, targetWidth, targetHeight);
            
            pica.resize(tempCanvas, this.canvas, {
              quality: 3,
              alpha: true,
              unsharpAmount: 80,
              unsharpRadius: 0.6,
              unsharpThreshold: 2
            })
            .then(() => {
              //  JPEG 
              const dataURL = this.canvas.toDataURL('image/jpeg', 0.95);
              
              this.$firestoreRefs.photos.add({
                url: file.name,
                src: dataURL,
              });
              window.alert('');
            })
            .catch(err => {
              console.error(':', err);
              window.alert('');
            });
          };
          img.src = event.target.result;
        };
        reader.readAsDataURL(processedFile);
      } catch (error) {
        console.error(':', error);
        window.alert('');
      }
    },
  },
  mounted() {
    this.canvas = document.getElementById('canvas');
    this.ctx = this.canvas.getContext('2d');
  }
};

</script>

